.SignIn {
    margin: 20px auto;
    width: 80%;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
}

.SignIn--error{
    padding-left: 10px;
    color: red;
}

@media(min-width: 600px) {
    .SignIn {
        width: 500px;
    }
}